html {
  height: 100%;
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
.page-body {
  min-height: 100%;
  margin: 0;
  font-family: "Blogger Sans", "Arial", sans-serif;
  background-repeat: no-repeat;
  background-color: #161C35;
}
@media (min-width: 768px) {
  .page-body {
    background-color: #182044;
    background-image: url("../img/globe-back-tablet@1x.jpg");
    background-position: center 382px;
    background-size: auto;
  }
}
.page-body--form {
  background-color: #EDEFF6;
  background-image: none;
  background-size: initial;
}
.page-body--catalog {
  background-color: #EDEFF6;
  background-image: none;
  background-size: initial;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
  color: inherit;
}
blockquote,
h1,
h2,
h3,
p {
  margin: 0;
}
input {
  font-family: inherit;
}
button {
  background-color: transparent;
  border: none;
}
textarea {
  font-family: inherit;
}
ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
fieldset {
  padding: 0;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.hide {
  display: none;
}
.page-heading {
  font-weight: 700;
  color: #ffffff;
}
.page-heading--mobile {
  font-size: 36px;
}
.block-heading--mobile {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  color: #FF8D30;
}
.block-text-mobile {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  color: #ffffff;
}
.block-text-mobile--bold {
  font-weight: 700;
}
.block-text-mobile--dark {
  color: #444444;
}
.button {
  font-family: inherit;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  background-color: #FFD74B;
  border: none;
  color: #161C35;
}
.button:hover {
  background-color: #FFBD30;
}
.button:active {
  color: rgba(22, 28, 53, 0.3);
}
.button--round {
  border-radius: 25px;
}
.button--mobile {
  font-size: 18px;
  font-weight: 700;
  color: #1D2E5B;
}
.button--border {
  border: 3px solid #192144;
}
.tooltip {
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  padding: 18px 25px;
  max-height: 56px;
  left: -65px;
  bottom: -66px;
  background-color: #101D41;
  visibility: hidden;
  color: #ffffff;
  text-align: center;
  border-radius: 2px;
  position: absolute;
  z-index: 1;
  word-wrap: break-word;
}
